export default {
	data() {
		return {
			staticURL: this.$url.staticURL,
			uploadURL: this.$url.uploadURL,
			gender: ['男', '女'],
			genderIndex: 0,
			name: 'carson',
			tel: '18561332353',
			card: '370826199511000031',
			cartImg: ''
		};
	},
	methods: {
		submit() {
			const vm = this;
			if(vm.tel.length != 11) {
				vm.$utils.toast('请将手机号输入完整');
				return false;
			} else if(!vm.cartImg){
				vm.$utils.toast('请上传手持身份证照片');
				return false;
			}
			if(!vm.name || !vm.card) {
				vm.$utils.toast('请将信息填写完整');
				return false;
			}
			uni.showModal({
			    content: '确认提交',
			    success: function (res) {
			        if (res.confirm) {					
						vm.$utils.toast('信息提交成功,预计将在24h内予以审核');
						vm.$utils.goPage(`/pages/videoAll/auditResult/auditResult`)
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		},
		// 拍照
		photograph() {
			const vm = this;
			uni.chooseImage({
				count: 1,
				sourceType: ['camera'],
				success: function(res) {
					const tempFilePaths = res.tempFilePaths;
					vm.cartImg = tempFilePaths[0];
					console.log(tempFilePaths);
					// this.$utils.uploadingImg();
				},
				fail: function() {}
			})
		},
		changeGender(e) {
			this.genderIndex = e.target.value
		},
		goPage() {
			this.$utils.goPage(`/pages/mainAll/addressAdd/addressAdd`)
		}
	}
}
